export default {
	data() {
		return {
			userinfo:{
				avatar:'../../static/test.jpg'
			},
			phoneHeight:0,
			isshow:true
		};
	},
	onReady() {
		this.getHeight()
	},
	methods:{
		getHeight(){
			uni.getSystemInfo({
			    success:  (res) =>{
					this.phoneHeight = res.windowHeight - 230
			    }
			});
		},
		goabout(){
			uni.navigateTo({
				url:'about/about'
			})
		},
		gouserinfo(){
			uni.navigateTo({
				url:'userinfo/userinfo'
			})
		},
		edit(){
			uni.navigateTo({
				url:'edit/edit'
			})
		},
		login(){
			uni.navigateTo({
				url:'../login/login'
			})
		}
	}
}
